/*ESTILOS GENERALES*/

body {
  font-family: sans-serif, Arial, Helvetica;
  padding: 0px;
  margin: 0px;
}

.layout {
  width: 85%;
  margin: 0px auto;
}

.header {
  height: 132px;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  border-bottom: 3px solid black;
  margin-bottom: 35px;
}

.header .logo {
  flex-grow: 2;
  display: flex;
}
.header .logo span {
  display: block;
  font-size: 90px;
  font-weight: bolder;
  margin-top: -8px;
}
.header .logo h3 {
  display: block;
  flex-basis: 40px;
  margin-left: 16px;
  margin-top: 1.5rem;
  font-weight: 500;
  text-transform: uppercase;
}
.header nav {
  flex-grow: 1;
}
.header nav ul {
  height: 132px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}
.header li {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
}

.header li a {
  color: black;
  text-decoration: none;
  margin-left: 15px;
  margin-right: 15px;
  transition: all 300ms;
}

.header .active,
.header a:hover {
  color: #900c3f;
}
.content {
  min-height: 400px;
}
.footer {
  text-align: center;
  font-size: 14px;
  margin-top: 80px;
  border-top: 3px solid black;
  padding-top: 45px;
  background: #f4f4f4;
  color: #444;
  min-height: 40px;
  line-height: 7px;
}

.home h1 {
  font-family: Georgia;
  font-weight: 400;
  font-size: 50px;
  margin: 80px 0 0 0;
}
.home strong {
  color: #900c3f;
}
.home h2 {
  color: rgba(98, 97, 97, 0.57);
  font-weight: lighter;
}

.home .title a {
  background-color: #900c3f;
  text-decoration: none;
  color: white;
  padding: 6px;
  border-radius: 5px;
}
.home .title a:hover {
  color: #900c3f;
  background-color: rgba(246, 170, 170, 1);
  text-decoration: none;
  padding: 6px;
  border-radius: 5px;
  transition: all 300ms;
}
.page .heading,
.home .heading {
  font-size: 40px;
  font-weight: 500;
  margin: 0px;
  color: #000;
}
.page .heading {
  font-size: 55px;
  font-weight: bold;
}
.home p {
  color: rgba(98, 97, 97, 0.57);
}
.container-form {
  margin: 0px auto;
  text-align: center;
  width: 27%;
}

input[type="text"],
textarea {
  padding: 15px;
  display: block;
  width: 250px;
  margin: 30px 0 30px 0;
}

input[type="submit"],
button {
  background-color: #900c3f;
  text-decoration: none;
  color: white;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  border-radius: 15px;
  margin: 0px 12rem 0rem 0px;
  cursor: pointer;
}
input[type="submit"]:hover,
button:hover {
  color: #900c3f;
  background-color: rgba(246, 170, 170, 1);
  font-size: 15px;
  font-weight: 600;
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  border-radius: 15px;
  margin: 0px 12rem 0rem 0px;
  cursor: pointer;
  transition: all 300ms;
}
/*  */
.works {
  display: flex;
  flex-wrap: wrap;
}
.work-item {
  flex-basis: 30%;
  flex-grow: 1;
  margin: 30px;
}
.work-item .mask {
  width: 100%;
  height: 350px;
  overflow: hidden;
  border: 1px solid #ddd;
  box-shadow: 0px 5px 15px #ddd;
  border-radius: 15px;
}
.work-item img {
  width: 100%;
}
.work-item .mask:hover img {
  width: 150%;
  margin: 0% 0% 30% 0%;
  transition: all 500ms;
}
.work-item span {
  color: gray;
  display: block;
  margin: 2rem 0 0 0;
}

.work-item h2 {
  color: black;
  margin: 8px 0 0 0;
  display: block;
  font-size: 30px;
}
.work-item h3 {
  color: #777;
  margin: 25px 0 0 0;
  display: block;
}
.work-item a {
  color: black;
  text-decoration: none;
  transition: all 300ms;
}
.work-item a:hover {
  color: #900c3f;
  text-decoration: none;
  transition: all 300ms;
}

.page-work .mask {
  height: 300px;
  overflow: hidden;
  box-shadow: 0 0 20px #ddd;
}
.description a {
  text-decoration: none;
  color: black;
  transition: all 300ms;
  font-size: 25px;
}
.description a:hover {
  color: #900c3f;
  background-color: rgba(246, 170, 170, 1);
  text-shadow: 1px 1px 1px #000;
  text-decoration: none;
  transition: all 300ms;
  font-size: 25px;
  font-weight: 700;
  letter-spacing: 2px;
  padding: 0.2rem 0.5rem;
  border-radius: 15px;
}
